


























import { Vue, Component, Prop } from "vue-property-decorator";
import newHaveDraw from "@/components/mobile/haveDraw/index";
import router from "@/router";

@Component({})
export default class HaveDraw extends Vue {
  show = true;

  handleNo() {
    newHaveDraw.close();
    router.push("/thanks");
  }

  handleYes() {
    router.push("/draw");
    newHaveDraw.close();
  }
}
